body {
    position: relative;
    background-color: #eee;
    background-color: var(--backgroundGray);
    background-size: cover;
    font-family: proxima-nova, sans-serif;
}

.center-block {
    margin: 0 auto;
    float: none;
}

/** Media Queries */
@media only screen and (max-width: 600px) {
    html {
        font-size: 12px;
    }
}

/** Global Variables */
:root {
    --primaryBlue: #01579b;
    --lakeBlue: #004baf;
    --riverBlue: #097dbc;
    --melloBlue: #049fd9;
    --mediumBlue: #34adde;
    --statusBlue: #64bbe3;
    --icyBlue: #c4d6ed;
    --darkGray1: #39393b;
    --darkGray2: #626469;
    --mediumGray1: #9e9ea2;
    --lightGray1: #c6c7ca;
    --lightGray2: #e9e9e9;
    --paleGray1: #e8ebf1;
    --backgroundGray: #eee;
    --foregroundWhite: #fafafa;
}

.login-container {
    border-radius: 0.5rem;
    margin: 10% auto 0.5% auto;
    float: none;
    width: 90%;
    max-width: 27rem;
    padding: 3rem 3rem;
    overflow: hidden;
    background: var(--foregroundWhite);
    box-shadow: rgba(0, 0, 0, 0.1) 0 0.1rem 0.2rem 0,
        rgba(0, 0, 0, 0.05) 0 0.1rem 0.2rem 0;
    border: 0.06rem solid #ebebeb;
}

.login-container .title {
    color: var(--mediumBlue);
    font-family: proxima-nova;
    font-weight: 600;
    padding: 0;
    line-height: normal;
    font-size: 5.25rem;
    text-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15);
    margin: 3rem auto 2.5rem auto;
}

.login-btn {
    border-radius: 0.5rem;
    overflow: hidden;
    width: 90%;
    max-width: 60%;
    margin: 2.5rem auto 5rem auto;
    background: var(--mediumBlue);
    border: 0;
    border-bottom: 0.15rem solid var(--riverBlue);
    box-shadow: rgba(0, 0, 0, 0.2) 0 0.06rem 0.06rem 0,
        rgba(0, 0, 0, 0.05) 0 0.06rem 0.1rem 0;
    padding: 0.75rem 0.5rem;
    font-size: 1rem;
    color: white;
}

.login-btn:hover {
    background: var(--melloBlue);
    color: white;
}

.created-by {
    text-align: center;
    margin: 0;
    color: var(--lightGray1);
}

.created-by a {
    color: inherit;
    text-decoration: none;
    -webkit-transition: border 0.1s linear;
    transition: border 0.1s linear;
    border-bottom: 0.05rem dotted rgba(198, 199, 202, 0);
}

.created-by a:hover {
    border-bottom: 0.05rem dotted rgba(198, 199, 202, 1);
}

@media only screen and (max-width: 600px) {
    .login-container {
        max-width: 75%;
    }
}

.navbar {
    background: rgba(9, 125, 188, 0.7);
    text-align: center;
    padding: 0.5rem 1rem;
    box-shadow: 0 0.06rem 0.3rem 0 rgba(0, 0, 0, 0.15),
        0 0.1rem 0.1rem 0 rgba(0, 0, 0, 0.1),
        0 0.2rem 0.1rem -0.1rem rgba(0, 0, 0, 0.08);
    margin-bottom: 1.5rem;
}

.navbar-title {
    line-height: 0;
    font-weight: 500;
    font-size: 1.75rem;
    color: white;
    margin: 0 auto;
    opacity: 0.9;
    text-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.15);
}

.select-board {
    width: 100%;
    max-width: 50%;
    margin: 0 auto 0 0;
    cursor: pointer;
    display: inline-block;
    font-size: 0.8rem;
    height: 1.75rem;
    vertical-align: middle;
}

.account-icon {
    color: white;
    opacity: 0.9;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.account-icon:hover {
    color: var(--lightGray1);
}

@media only screen and (max-width: 600px) {
    .select-board {
        display: inline-block;
        max-width: 100%;
        height: 2rem;
    }

    .col-sm-3 {
        max-width: 25%;
    }

    .col-sm-6 {
        max-width: 50%;
    }
}

