.list-item {
    border: 0.06rem solid lightgrey;
    border-radius: 0.3rem;
    margin-bottom: 0.4rem;
    padding: 0.4rem;
    background-color: white;
    -webkit-transition: background-color 0.2s ease;
    transition: background-color 0.2s ease;
    font-size: 0.9rem;
    overflow: hidden;
    vertical-align: middle;
    cursor: -webkit-grab;
    cursor: grab;
    box-shadow: 0 0.025rem 0.05rem rgba(0, 0, 0, 0.04),
        0 0.025rem 0.05rem rgba(0, 0, 0, 0.08);
}

.list-item p {
    display: inline-block;
    max-width: 90%;
    margin: 0;
    padding: 0;
}

.list-item:hover {
    background-color: var(--lightGray2);
}

.list-item i {
    font-size: 1.4rem;
    vertical-align: middle;
    margin: auto 0.2rem auto 0;
    float: right;
    clear: both;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: all 0.15s linear;
    transition: all 0.15s linear;
    cursor: pointer;
}

.list-item:hover i {
    visibility: visible;
    opacity: 1;
}

.list-item:hover i:hover {
    color: var(--statusBlue);
}

.on-drag {
    box-shadow: 0 0.1rem 0.2rem -0.05rem rgba(0, 0, 0, 0.05),
        0 0.2rem 0.25rem 0 rgba(0, 0, 0, 0.1);
}

.t-list {
    width: 100%;
    max-width: 100%;
    box-shadow: 0 0.05rem 0.2rem 0 rgba(0, 0, 0, 0.2),
        0 0.1rem 0.1rem 0 rgba(0, 0, 0, 0.14),
        0 0.15rem 0.05rem -0.1rem rgba(0, 0, 0, 0.12);
    border: 0.06rem solid #ebebeb;
    height: 30%;
    background-color: rgba(244, 247, 252, 0.97);
}

.card-header {
    height: 3rem;
    padding: 0.5rem 0.75rem;
    margin: 0;
    background-color: var(--foregroundWhite);
    cursor: -webkit-grab;
    cursor: grab;
}

.card-title {
    font-size: 0.9rem;
    font-weight: 600;
    margin: 0;
    line-height: 2rem;
    color: var(--darkGray1);
}

.list-item-container {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    padding: 0.5rem;
    min-height: 2rem;
    width: 100%;
}

.new-task-container {
    width: 100%;
    max-width: 100%;
    padding: 0.1rem;
}

.btn-new-task {
    margin: 0;
    padding: 0;
}

.add-icon {
    font-size: 1.3rem;
    font-weight: 600;
    color: var(--darkGray1);
    vertical-align: middle;
    -webkit-transition: all 0.15s linear;
    transition: all 0.15s linear;
}

.add-icon:hover {
    color: var(--statusBlue);
}

.new-task-input {
    width: 100%;
    max-width: 100%;
    padding: 0 0.25rem;
    font-size: 0.9rem;
    margin: 0;
    height: 2rem;
}

.btn-new-task-submit {
    max-width: 50%;
    margin-top: 0.4rem;
    padding: 0 0.3rem;
    color: white;
    background-color: var(--mediumBlue);
}

.container-board {
    max-width: 85%;
    padding: 0;
    margin: 1rem auto;
}

.grid-sizer,
.grid-item {
    width: 18.8%;
}

.gutter-sizer {
    width: 1.5%;
}

@media only screen and (max-width: 600px) {
    .grid-sizer,
    .grid-item {
        width: 100%;
    }

    .gutter-sizer {
        width: 5%;
    }
}

.select-board-message {
    text-align: center;
    margin: 25rem auto;
    font-size: 2.5rem;
    color: var(--lightGray1);
}

